                    <div className="bg-blue-600 text-white p-3 sm:p-4 rounded-full mb-3 sm:mb-4 mx-auto w-12 h-12 sm:w-16 sm:h-16 flex items-center justify-center">
                      <span className="text-xl sm:text-2xl">üìç</span>
                    </div>
